.myModal {
  position: fixed;
  top: 0;
  left: 0;
  bottom: 0;
  right: 0;

  display: none;

  background-color: rgba(0, 0, 0, 0.5);
}

.myModal.active {
  display: flex;
  justify-content: center;
  align-items: center;
}

.myModalContent {
  min-width: 300px;
  padding: 25px;

  background-color: white;
  border-radius: 16px;
}
